.App {
  text-align: center;
  overflow: hidden;
  box-sizing: border-box;
  background-color: rgb(28, 19, 39);
  color: white;
  height: 100vh;
  display: flex;
}
.bar{
  height: 100vh;
  width: 20vw;
  background-image: linear-gradient( rgba(81, 79, 99, 0.5),  rgba(29, 26, 78, 0.5));
  font-family: 'Roboto Mono', monospace;
  padding: 2rem;
  font-weight: 300;
  h2{
    margin: 40px;
  }
}
  .dashboard{
    font-family: 'Roboto Mono', monospace;
    border-radius: 30px;
    padding: 10px;
    margin: 30px;
    box-shadow:0 0 25px rgb(63, 61, 90), 0 0 25px rgb(63, 61, 90), 0 0 5px rgb(56, 53, 92);
    width: 100%;
    height: 80%;
    box-sizing: border-box;
    display: flex;
  }
  h1{
    font-family: 'Roboto Mono', monospace;
    letter-spacing: 3px;
    margin: 2rem;
  }

